import { useCallback, useState, useEffect } from 'react';
import { orange, grey } from '@material-ui/core/colors';
import { EE, localStore } from '@src/utils';
import { THEME_TYPE_STORED_KEY } from '@src/configs/env';

const getThemeConfig = (/** @type {import('@material-ui/core').PaletteType} */ type) => {
  return /** @type {import('@material-ui/core/styles').ThemeOptions} */ ({
    palette: {
      type,
      primary: {
        main: type === 'light' ? orange['A700'] : grey['900'],
        light: orange['A700'],
        dark: grey['900'],
        contrastText: '#fff',
      },
      secondary: {
        main: type === 'light' ? orange['A400'] : 'rgba(255, 255, 255, 0.7)',
        light: orange['A400'],
        dark: 'rgba(255, 255, 255, 0.7)',
        contrastText: '#000',
      },
      background: {
        default: type === 'light' ? grey['300'] : grey['700'],
      },
      contrastThreshold: 3,
      tonalOffset: 0.2,
    },
  });
};

export function useTheme() {
  const [theme, setTheme] = useState(() => {
    // get initial theme type
    let type = localStore.get(THEME_TYPE_STORED_KEY);
    if (!['light', 'dark'].includes(type)) {
      type = 'light';
    }

    return getThemeConfig(type);
  });

  const toggleTheme = useCallback(() => {
    const { type } = theme.palette;
    const nextType = type === 'light' ? 'dark' : 'light';

    // switch theme type
    setTheme(getThemeConfig(nextType));

    // remeber theme type
    localStore.set(THEME_TYPE_STORED_KEY, nextType);
  }, [theme, setTheme]);

  useEffect(() => {
    EE.on('toggle-theme', toggleTheme);
    return () => EE.off('toggle-theme', toggleTheme);
  }, [toggleTheme]);

  return { theme, toggleTheme };
}
